/* eslint-disable react-hooks/exhaustive-deps */
import React from "react";
import { injectIntl } from "react-intl";
import ImageLoader from "react-loading-image";
import styled from "styled-components";
import {
  Row,
  Col,
  Upload,
  Divider,
  message,
  Form,
  Input,
  Alert,
  Button,
  Select,
  Icon,
  Badge,
  Modal,
  List,
  Radio,
  Steps
} from "antd";

import { sendFile, getApiEngines, getProcessByListId } from "./../../services";
import functions from "./../../utils/functions";
import "./index.css";

const { Step } = Steps;
const Dragger = Upload.Dragger;
const FormItem = Form.Item;
const Option = Select.Option;

const REACT_APP_API = process.env.REACT_APP_API;

const FormItemWrapper = styled(FormItem)`
  margin-bottom: 5px !important;
  & {
    .ant-form-item-label {
      line-height: 20px;
    }
    .ant-form-explain {
      color: #0f416a;
    }
  }
`;

const Div = styled.div`
  width: 100%;
  ${props => (props.error ? `  border: 2px dashed #ea2020` : null)};
`;
const DivList = styled.div`
  height: 200px;
  margin-bottom: 10px;
  overflow-y: auto;
`;
const DivError = styled.div`
  h2,
  p {
    text-align: center;
  }
`;

const DivButtons = styled.div`
  display: flex;
  justify-content: center;
`;

const IL = styled(ImageLoader)`
  width: 100%;
  height: auto;
`;
const ModalWrapper = styled(Modal)`
  & {
    .ant-modal-body {
      max-height: 410px;
      padding: 10px 20px 10px 20px;
      margin-bottom: 12px;
      overflow: auto;
    }
  }
`;

const jsUcfirst = string => {
  return string.charAt(0).toUpperCase() + string.slice(1);
};

class Process extends React.Component {
  state = {
    allowedFiles: "",
    showBilling: "yes",
    secret: null,
    process: [],
    fileList: [],
    uploading: false,
    visible: false,
    payVisible: false,
    processEnd: false,
    showError: false,
    source: null,
    target: null,
    viewNoty: "process",
    loading: false,
    engines: [],
    defaultValue: [],
    defaultSourceValue: null,
    defaultTargetValue: null,
    selected: {
      engines: []
    },
    values: null,
    current: 0,
    processSelected: null,
    loadingPay: false
  };

  componentDidMount = () => {
    this.getEngines();
    this.setState({
      secret: Math.random()
        .toString(36)
        .slice(-8)
    });
  };

  getEngines = async () => {
    this.setState({
      loading: true
    });
    try {
      const engines = await getApiEngines();
      if (engines.data.process && engines.data.process.length) {
        this.setState({
          engines: engines.data.process,
          allowedFiles: engines.data.allowedFiles,
          defaultValue: jsUcfirst(engines.data.value)
        });
        this.initSelected(engines.data.value, engines.data.process);
      }

      this.setState({
        loading: false
      });
    } catch (err) {
      this.setState({
        loading: false
      });
    }
  };

  getProcess = async () => {
    const self = this;
    setInterval(async () => {
      const aux = await getProcessByListId({
        secret: self.state.secret
      });
      this.setState({
        process: aux.data
      });
    }, 5000);
  };

  handleSubmit = e => {
    e.preventDefault();
    const {
      intl: { messages }
    } = this.props;
    const { target, fileList, secret, selected } = this.state;
    const targets = this.getTargets();

    // eslint-disable-next-line array-callback-return
    const nameEng = targets.filter(item => {
      if (item && item.key === target) {
        return item;
      }
    })[0];

    if (nameEng) {
      this.setState({
        target,
        showError: false,
        viewNoty: "process"
      });
    }

    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (fileList.length) {
          const promises = fileList.map(item => {
            return functions.getBase64(item.originFileObj);
          });
          Promise.all(promises)
            .then(files => {
              let aux = values.target.split("-");
              let form = {
                secret: secret,
                process: {
                  id: aux[0],
                  name: selected.name
                },
                engine: nameEng,
                source: {
                  id: aux[0],
                  name: selected.name
                },
                files: files,
                email: values.email
              };
              this.setState({
                uploading: true
              });
              sendFile(form)
                .then(() => {
                  message.success(messages.default.success);
                  this.setState({
                    uploading: false,
                    viewNoty: "success",
                    fileList: []
                  });
                  this.getProcess();
                })
                .catch(error => {
                  message.error(error.message);
                  this.setState({
                    uploading: false,
                    viewNoty: "error"
                  });
                });
            })
            .catch(err => {
              console.error(err);
            });
        } else {
          this.setState({
            showError: true
          });
        }
      }
    });
  };

  handleSubmitBilling = e => {
    e.preventDefault();
  };

  handleChange = value => {
    let selected = null;
    let array = [];

    this.state.engines.forEach(element => {
      if (element.name === value.toLowerCase()) {
        this.setState({
          selected: element
        });
        selected = element;
      }
    });

    if (selected) {
      if (selected.engines && selected.engines.length) {
        selected.engines.forEach(element => {
          let flag = false;
          for (let i = 0; i < array.length; i++) {
            if (array[i].key === element.source) {
              flag = true;
              break;
            }
          }
          if (!flag) {
            let key = element.source;
            array.push({
              key,
              id: element.id,
              name: functions.languages[key],
              target: element.target,
              source: element.source
            });
          }
        });
        if (array.length) {
          this.setState({
            defaultSourceValue: array[0].name,
            source: array[0].source,
            target: `${array[0].id}-${array[0].target}`,
            defaultTargetValue: this.getTargets(
              selected.engines,
              array[0].source
            )[0].name
          });
        }
      }
    }
  };

  initSelected = (value, sources) => {
    let selected = null;
    let array = [];
    const self = this;
    for (let i = 0; i < sources.length; i++) {
      if (sources[i].name === value) {
        self.setState({
          selected: sources[i]
        });
        selected = sources[i];
        break;
      }
    }

    if (selected) {
      if (selected.engines && selected.engines.length) {
        selected.engines.forEach(element => {
          let flag = false;
          for (let i = 0; i < array.length; i++) {
            if (array[i].key === element.source) {
              flag = true;
              break;
            }
          }
          if (!flag) {
            let key = element.source;
            array.push({
              key,
              id: element.id,
              name: functions.languages[key],
              target: element.target,
              source: element.source
            });
          }
        });
        if (array.length) {
          this.setState({
            defaultSourceValue: array[0].name,
            source: array[0].source,
            target: `${array[0].id}-${array[0].target}`,
            defaultTargetValue: this.getTargets(
              selected.engines,
              array[0].source
            )[0].text
          });
        }
      }
    }
  };

  changeSource = source => {
    this.setState({
      source
    });
  };

  changeTarget = target => {
    this.setState({
      target
    });
  };

  getSources = () => {
    const { selected } = this.state;
    let array = [];
    if (selected.engines && selected.engines.length) {
      selected.engines.forEach(element => {
        let flag = false;
        for (let i = 0; i < array.length; i++) {
          if (array[i].key === element.source) {
            flag = true;
            break;
          }
        }
        if (!flag) {
          let key = element.source;
          array.push({ key, name: functions.languages[key] });
        }
      });
    }

    return array;
  };

  getTargets = (engines = null, _source = null) => {
    const { selected, source } = this.state;
    if (!engines) {
      engines = selected.engines;
    }
    if (!_source) {
      _source = source;
    }

    let array = [];
    engines.forEach(element => {
      let flag = false;
      for (let i = 0; i < array.length; i++) {
        if (array[i].key === element.target) {
          flag = true;
          break;
        }
      }

      if (_source) {
        if (!flag && element.source === _source) {
          if (element.domain) {
            element["key"] = `${element.id}-${element.target}`;
            element["text"] = `${functions.languages[element.target]} - ${
              element.name
            } - ${element.domain}`;
          } else {
            element["key"] = `${element.id}-${element.target}`;
            element["text"] = `${functions.languages[element.target]} - ${
              element.name
            }`;
          }
          array.push(element);
        }
      }
    });
    return array;
  };

  renderForms = () => {
    const {
      intl: { messages }
    } = this.props;
    const {
      allowedFiles,
      engines,
      loading,
      fileList,
      visible,
      showError,
      defaultValue,
      source,
      processEnd,
      defaultSourceValue,
      defaultTargetValue,
      selected,
      uploading,
      target
    } = this.state;

    const {
      form: { getFieldDecorator, getFieldsError }
    } = this.props;

    const formItemLayout = {
      labelCol: {
        span: 24
      },
      wrapperCol: {
        span: 24
      }
    };
    const formItemLayout2 = {
      wrapperCol: {
        xs: 24
      }
    };

    const dprops = {
      beforeUpload: file => {
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!functions.checkFile(file, allowedFiles)) {
          message.error(
            messages.process.error.uploadOne.replace(
              "allowedFiles",
              allowedFiles
            )
          );
        } else if (!isLt2M) {
          message.error(messages.process.error.uploadTwo);
        }
        return false;
      },
      onChange: info => {
        let aux = [];
        info.fileList.map(item => {
          if (functions.checkFile(item, allowedFiles)) {
            aux.push(item);
          }
          return aux;
        });
        this.setState({
          fileList: aux
        });
      },
      multiple: true,
      fileList,
      showUploadList: false
    };
    let options = [];
    if (engines.length) {
      options = engines.map(d => (
        <Option key={d.id} value={d.name.replace(/^\w/, c => c.toUpperCase())}>
          {d.name.replace(/^\w/, c => c.toUpperCase())}
        </Option>
      ));
    }

    return (
      <React.Fragment>
        <ModalWrapper
          title="Files List"
          onCancel={this.hideListFile}
          visible={visible}
          footer={null}
          closable
        >
          <div className="ant-upload-list ant-upload-list-text">
            {fileList.map(item => {
              return (
                <div
                  key={item.uid}
                  className="ant-upload-list-item ant-upload-list-item-undefined"
                >
                  <div className="ant-upload-list-item-info">
                    <span>
                      <Icon type="paper-clip" />
                      <span
                        className="ant-upload-list-item-name"
                        title={item.name}
                      >
                        {item.name}
                      </span>
                    </span>
                  </div>
                  <Icon
                    onClick={() => {
                      this.onRemove(item);
                    }}
                    type="close"
                  />
                </div>
              );
            })}
          </div>
        </ModalWrapper>
        <Form {...formItemLayout} onSubmit={this.handleSubmit}>
          <Row type="flex" justify="center" align="middle">
            <Col xs={24}>
              <FormItemWrapper {...formItemLayout2}>
                <Div error={showError && !fileList.length}>
                  <Badge style={{ cursor: "pointer" }} count={fileList.length}>
                    <Dragger {...dprops}>
                      <p className="ant-upload-drag-icon">
                        <Icon type="inbox" />
                      </p>
                      <p className="ant-upload-text">{messages.process.drag}</p>
                    </Dragger>
                    {fileList.length > 0 && (
                      <Button
                        onClick={this.showListFile}
                        style={{ marginTop: 5 }}
                        size="small"
                        type="primary"
                        icon="eye"
                      >
                        Files List
                      </Button>
                    )}
                  </Badge>

                  {showError && !fileList.length ? (
                    <Alert
                      style={{ margin: 5 }}
                      message={messages.process.alert}
                      type="error"
                      showIcon
                    />
                  ) : null}
                </Div>
              </FormItemWrapper>
            </Col>
            <Col xs={24}>
              <FormItemWrapper
                validateStatus={loading ? "validating" : ""}
                label={messages.process.process}
                hasFeedback
              >
                {getFieldDecorator("process", {
                  initialValue: defaultValue,
                  disabled: source === null || processEnd,
                  rules: [
                    {
                      required: true,
                      message: messages.process.rules.process.required
                    }
                  ]
                })(
                  <Select autofocus="true" onChange={this.handleChange}>
                    {options}
                  </Select>
                )}
              </FormItemWrapper>
            </Col>

            <Col xs={24}>
              <FormItemWrapper label={messages.process.source} hasFeedback>
                {getFieldDecorator("source", {
                  initialValue: defaultSourceValue,
                  rules: [
                    {
                      required: true,
                      message: messages.process.rules.source.required
                    }
                  ]
                })(
                  <Select
                    disabled={!this.getSources().length}
                    onChange={this.changeSource}
                  >
                    {this.getSources().map(element => {
                      return <Option key={element.key}>{element.name}</Option>;
                    })}
                  </Select>
                )}
              </FormItemWrapper>
              <FormItemWrapper label={messages.process.target} hasFeedback>
                {getFieldDecorator("target", {
                  initialValue: defaultTargetValue,
                  rules: [
                    {
                      required: true,
                      message: messages.process.rules.target.required
                    }
                  ]
                })(
                  <Select
                    onChange={this.changeTarget}
                    disabled={source === null || processEnd}
                  >
                    {this.getTargets().map(element => {
                      return (
                        <Option key={element.key} value={element.key}>
                          {element.text}
                        </Option>
                      );
                    })}
                  </Select>
                )}
              </FormItemWrapper>
              <FormItemWrapper
                label={messages.process.notification}
                hasFeedback
                help={messages.process.notificationHelp}
              >
                {getFieldDecorator("email", {
                  disabled: processEnd,
                  rules: [
                    {
                      type: "email",
                      message: messages.process.rules.email.type
                    },
                    {
                      required: true,
                      message: messages.process.rules.email.required
                    }
                  ]
                })(<Input disabled={!selected.engines.length} />)}
              </FormItemWrapper>
            </Col>
            <Col xs={24}>
              <FormItemWrapper
                style={{
                  display: "flex",
                  justifyContent: "center",
                  marginBottom: 0
                }}
              >
                {!processEnd ? (
                  <Row justify="center" type="flex">
                    <Button
                      style={{ marginTop: 10 }}
                      icon="upload"
                      type="primary"
                      htmlType="submit"
                      size="large"
                      loading={uploading}
                      disabled={
                        !this.getSources().length ||
                        !target ||
                        !source ||
                        !this.props.form.getFieldValue("email") ||
                        !this.props.form.getFieldValue("email").length ||
                        functions.hasErrors(getFieldsError())
                      }
                    >
                      {uploading
                        ? messages.process.uploading
                        : messages.process.uploadingLoad}
                    </Button>
                  </Row>
                ) : null}
              </FormItemWrapper>
            </Col>
          </Row>
        </Form>
      </React.Fragment>
    );
  };

  showListFile = () => {
    if (this.state.fileList.length) {
      this.setState({
        visible: true
      });
    }
  };

  hideListFile = () => {
    this.setState({
      visible: false
    });
  };

  setViewNoty = view => {
    this.setState({
      viewNoty: view
    });
  };

  onRemove = file => {
    const { processEnd, fileList } = this.state;
    if (!processEnd) {
      let aux = [];
      fileList.map(item => {
        if (item.uid !== file.uid) {
          aux.push(item);
        }
        return aux;
      });
      if (!aux.length) this.hideListFile();
      this.setState({
        fileList: aux
      });
    }
  };

  onRadioChange = e => {
    console.log("radio checked", e.target.value);
    this.setState({
      showBilling: e.target.value
    });
  };

  beforePay = item => {
    this.setState({
      payVisible: true,
      processSelected: item
    });
  };

  pay = async id => {
    this.setState({
      loadingPay: id
    });
    document.getElementById(id).submit();
  };

  next() {
    if (this.state.current === 1) {
      console.log(this);
      const { form } = this.props;
      form.validateFields((err, values) => {
        if (err) {
          return;
        }

        console.log("Received values of form: ", values);
        // form.resetFields();
        const current = this.state.current + 1;
        this.setState({ current, values });
      });
    } else {
      let sum = 1;
      if (this.state.current === 0 && this.state.showBilling === "no") {
        sum = 2;
      }
      const current = this.state.current + sum;
      this.setState({ current });
    }
  }

  prev() {
    let rest = 1;
    if (this.state.current === 2 && this.state.showBilling === "no") {
      rest = 2;
    }
    const current = this.state.current - rest;
    this.setState({ current });
  }

  render() {
    const {
      intl: { messages }
    } = this.props;
    const {
      viewNoty,
      process,
      current,
      processSelected,
      secret,
      loadingPay
    } = this.state;
    if (viewNoty === "process") {
      return this.renderForms();
    }
    if (viewNoty === "error") {
      return (
        <DivError>
          <IL
            src="/images/undraw_bug_fixing_oc7a.svg"
            loading={() => <div>{messages.process.loading}...</div>}
            error={() => <div>Error</div>}
          />
          <h2>{messages.process.errorProcess}</h2>
          <p>{messages.process.errorProcessSub}</p>
          <Divider />
          <DivButtons>
            <Button
              onClick={() => {
                this.setViewNoty("process");
              }}
              type="primary"
              size="large"
            >
              {messages.process.tryAgain}
            </Button>
          </DivButtons>
        </DivError>
      );
    }
    if (viewNoty === "success") {
      const radioStyle = {
        display: "block",
        height: "30px",
        lineHeight: "30px"
      };

      const {
        form: { getFieldDecorator, getFieldsError }
      } = this.props;

      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 }
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 }
        }
      };
      const steps = [
        {
          title: "Question",
          icon: <Icon type="question-circle" />,
          content: (
            <React.Fragment>
              <h4>Are you a citizen or company of the European Union?</h4>
              <Radio.Group
                onChange={this.onRadioChange}
                defaultValue={this.state.showBilling}
              >
                <Radio style={radioStyle} value="yes">
                  Yes
                </Radio>
                <Radio style={radioStyle} value="no">
                  No
                </Radio>
              </Radio.Group>
            </React.Fragment>
          )
        },
        {
          title: "Verification",
          icon: <Icon type="solution" />,
          content: (
            <Form {...formItemLayout}>
              {/* nombre, dirección, pais, VAT/TAX number */}
              <Form.Item label="Name">
                {getFieldDecorator("name", {
                  rules: [
                    {
                      required: true,
                      message: "Please input your Name!"
                    }
                  ]
                })(<Input />)}
              </Form.Item>
              <Form.Item label="Address">
                {getFieldDecorator("address", {
                  rules: [
                    {
                      required: true,
                      message: "Please input your Address!"
                    }
                  ]
                })(<Input />)}
              </Form.Item>
              <Form.Item label="Country">
                {getFieldDecorator("country", {
                  rules: [
                    {
                      required: true,
                      message: "Please input your Country!"
                    }
                  ]
                })(<Input />)}
              </Form.Item>
              <Form.Item label="VAT/TAX number">
                {getFieldDecorator("vattax", {
                  rules: [
                    {
                      required: true,
                      message: "Please input your VAT/TAX number!"
                    }
                  ]
                })(<Input />)}
              </Form.Item>
            </Form>
          )
        },
        {
          title: "Pay",
          icon: <Icon type="euro" />,
          content: (
            <Row justify="center" type="flex">
              {processSelected && processSelected.quotes
                ? processSelected.quotes.map((item, index) => {
                    return (
                      <Col key={item.optionid} xs={24} md={8}>
                        <Row justify="center" type="flex" align="middle">
                          <Col
                            style={{
                              textAlign: "center",
                              minHeight: 45
                            }}
                          >
                            <h4 style={{ minHeight: 41 }}>{item.option}</h4>

                            <form
                              hidden
                              id={`quote${index}`}
                              action={`${REACT_APP_API}/api/payment/pay`}
                              method="post"
                            >
                              <input type="text" name="secret" value={secret} />
                              <input
                                type="text"
                                name="uuid"
                                value={processSelected.uuid}
                              />
                              <input
                                type="text"
                                name="quote"
                                value={item.optionid}
                              />
                            </form>

                            <Button
                              loading={loadingPay === `quote${index}`}
                              onClick={() => {
                                this.pay(`quote${index}`);
                              }}
                              style={{ marginTop: 16 }}
                              type="primary"
                            >
                              <React.Fragment>
                                &nbsp;&nbsp;
                                <Icon type="euro" /> {item.price}
                              </React.Fragment>
                            </Button>
                          </Col>
                        </Row>
                      </Col>
                    );
                  })
                : null}
            </Row>
          )
        }
      ];

      return (
        <React.Fragment>
          <Modal
            width={600}
            title="Payment process"
            visible={this.state.payVisible}
            onCancel={this.handleCancel}
            footer={null}
          >
            <Steps current={current}>
              {steps.map(item => (
                <Step key={item.title} title={item.title} icon={item.icon} />
              ))}
            </Steps>

            <div className="steps-content">{steps[current].content}</div>
            <div className="steps-action">
              {current > 0 && (
                <Button style={{ marginLeft: 8 }} onClick={() => this.prev()}>
                  Previous
                </Button>
              )}
              {current < 3 && (
                <Button type="primary" onClick={() => this.next()}>
                  Next
                </Button>
              )}
              {current === 3 && (
                <Button
                  type="primary"
                  onClick={() => message.success("Processing complete!")}
                >
                  Done
                </Button>
              )}
            </div>
          </Modal>
          <DivError>
            <IL
              src="/images/undraw_confirmation_2uy0.svg"
              loading={() => <div>{messages.process.loading}...</div>}
              error={() => <div>Error</div>}
            />
            <h2>{messages.process.successProcess}</h2>
            <p>{messages.process.successProcessSub}</p>
            <Divider />
            {process.length > 0 && (
              <DivList>
                <List
                  size="small"
                  bordered
                  dataSource={process}
                  renderItem={item => (
                    <List.Item>
                      {item.fileName}{" "}
                      {item.status === "waiting" && (
                        <Button
                          disabled
                          size="small"
                          style={{ float: "right" }}
                          shape="circle"
                          icon="clock-circle"
                        />
                      )}
                      {item.status === "quote_ready" && (
                        <Button
                          onClick={() => {
                            this.beforePay(item);
                          }}
                          type="primary"
                          size="small"
                          style={{ float: "right" }}
                          shape="circle"
                          icon="euro"
                        />
                      )}
                    </List.Item>
                  )}
                />
              </DivList>
            )}
            {process.length === 0 && (
              <DivList>
                <Icon type="loading" /> Processing...
              </DivList>
            )}
            <DivButtons>
              <Button
                onClick={() => {
                  this.setViewNoty("process");
                }}
                type="primary"
                size="large"
              >
                {messages.process.another}
              </Button>
            </DivButtons>
          </DivError>
        </React.Fragment>
      );
    }
  }
}

const WrappedProcess = Form.create()(injectIntl(Process));

export default WrappedProcess;
