import React from "react";
import { injectIntl } from "react-intl";
import {
  Row,
  Col,
  Icon,
  Tooltip,
  Card,
  message,
  Spin,
  Table,
  Tag,
  Statistic
} from "antd";
import {
  ChartCard,
  Field,
  MiniArea,
  MiniBar,
  MiniProgress,
  Pie
} from "ant-design-pro/lib/Charts";
import Trend from "ant-design-pro/lib/Trend";
import NumberInfo from "ant-design-pro/lib/NumberInfo";

import numeral from "numeral";
import moment from "moment";
import styled from "styled-components";

import { getAdminDashboard } from "./../../services";
import functions from "./../../utils/functions";

import "./index.css";

const ColWrapper = styled(Col)`
  padding-left: 12px;
  padding-right: 12px;
  margin-bottom: 24px;
`;

class AdminDashboard extends React.Component {
  _isMounted = false;

  state = {
    loading: true,
    data: [],
    process: [],
    users: []
  };

  componentDidMount = () => {
    this.fetchData();
  };

  componentWillUnmount() {
    this._isMounted = false;
  }

  fetchData = async () => {
    this._isMounted = true;
    try {
      const response = await getAdminDashboard();
      if (this._isMounted) {
        console.log(response.data.data);
        this.setState({
          data: response.data.data.results,
          process: response.data.data.process,
          users: response.data.data.users,
          loading: false
        });
      }
    } catch (error) {
      message.error(error.message);
      if (this._isMounted) {
        this.setState({
          loading: false
        });
      }
    }
  };

  render() {
    const {
      intl: { messages }
    } = this.props;
    const { data, users } = this.state;

    const monthProcessData = [];
    const monthExpensesData = [];

    let countProcess = 0;
    let countProcessLW = 0;
    let countExpenses = 0;
    let countExpensesLW = 0;

    let countProcessComplete = 0;
    let countProcessCompleteLW = 0;

    let statusData = [];
    let percentComplete = 0;
    let percentCompleteLW = 0;

    if (data && data.length) {
      data.forEach((item, i) => {
        countProcessLW = countProcessLW + item.count;
        countExpensesLW = countExpensesLW + item.value;
        countProcessCompleteLW = countProcessCompleteLW + item.complete;

        countProcess = countProcess + item.count;
        countExpenses = countExpenses + item.value;

        countProcessComplete = countProcessComplete + item.complete;

        monthProcessData.push({
          x: moment(new Date(item.start)).format("YYYY-MM-DD"),
          y: Math.floor(item.count)
        });
        monthExpensesData.push({
          x: moment(new Date(item.start)).format("YYYY-MM-DD"),
          y: item.value.toFixed(2)
        });
        Object.keys(item.status).forEach(key => {
          statusData.push({
            x: functions.getStatusByText(key),
            y: item.status[key]
          });
        });
      });

      percentComplete = countProcess
        ? (100 * countProcessComplete) / countProcess
        : 0;

      percentCompleteLW = countProcess
        ? (100 * countProcessCompleteLW) / countProcess
        : 0;
    }
    const columns = [
      {
        title: messages.process.fileName,
        dataIndex: "fileName",
        key: "fileName",
        sorter: (a, b) => a.fileName - b.fileName
      },
      {
        title: messages.process.processName,
        dataIndex: "processName",
        key: "processName",
        render: (text, record) => (
          <span style={{ textTransform: "capitalize" }}>
            {record.processName}
          </span>
        )
      },
      {
        title: messages.process.language,
        key: "language",
        render: (text, record) => (
          <span style={{ textTransform: "capitalize" }}>
            <Tag style={{ cursor: "default" }} color="#108ee9">
              {record.engineSource}
            </Tag>{" "}
            <Icon type="arrow-right" />{" "}
            <Tag style={{ cursor: "default" }} color="#ff3f1b">
              {record.engineTarget}
            </Tag>
          </span>
        )
      },
      {
        title: messages.process.status,
        dataIndex: "status",
        key: "status",
        render: status => <span>{functions.getStatusByText(status)}</span>,
        filters: [
          {
            text: "Waiting",
            value: "waiting"
          },
          {
            text: "Waiting Quoting",
            value: "waiting_quoting"
          },
          {
            text: "Quotes Ready",
            value: "quotes_ready"
          },
          {
            text: "Pending Accept/Reject",
            value: "pending_accept_reject"
          },
          {
            text: "Preprocessing",
            value: "preprocessing"
          },
          {
            text: "Processing",
            value: "processing"
          },
          {
            text: "PostProcessing",
            value: "postProcessing"
          }
        ],
        onFilter: (value, record) => record.status === value
      }
    ];
    console.log("users", users);

    return (
      <Spin spinning={this.state.loading}>
        <Row style={{ margin: "12px -12px 0 -12px" }}>
          <ColWrapper xs={24} sm={7} md={7}>
            <ChartCard
              title={messages.dashboard.numberOfProcesses.toUpperCase()}
              total={numeral(countProcess).format("0,0")}
              contentHeight={134}
            >
              <NumberInfo
                subTitle={
                  <span>
                    {messages.dashboard.processesThisWeek.toUpperCase()}
                  </span>
                }
                total={numeral(countProcessLW).format("0,0")}
              />
              <MiniArea line height={45} data={monthProcessData} />
            </ChartCard>
          </ColWrapper>
          <ColWrapper xs={24} sm={7} md={7}>
            <ChartCard
              title={messages.dashboard.totalExpenses.toUpperCase()}
              action={
                <Tooltip
                  title={messages.dashboard.descriptionOfTheIndicator.toUpperCase()}
                >
                  <Icon type="info-circle-o" />
                </Tooltip>
              }
              total={`€ ${numeral(countExpenses).format("0.000")}`}
              footer={
                <Field
                  label={messages.dashboard.expensesthisWeek.toUpperCase()}
                  value={`€ ${numeral(countExpensesLW).format("0.000")}`}
                />
              }
              contentHeight={46}
            >
              <MiniBar height={46} data={monthExpensesData} />
            </ChartCard>
          </ColWrapper>
          <ColWrapper xs={24} sm={7} md={7}>
            <ChartCard
              title={messages.dashboard.processesCompleted.toUpperCase()}
              action={
                <Tooltip
                  title={messages.dashboard.descriptionOfTheIndicator.toUpperCase()}
                >
                  <Icon type="info-circle-o" />
                </Tooltip>
              }
              total={`${numeral(percentComplete).format("0.000")}%`}
              footer={
                <div>
                  <span>
                    {messages.dashboard.lastMonth.toUpperCase()}
                    <Trend
                      flag="up"
                      style={{ marginLeft: 8, color: "rgba(0,0,0,.85)" }}
                    >
                      {`${numeral(percentCompleteLW).format("0.000")}%`}
                    </Trend>
                  </span>
                </div>
              }
              contentHeight={46}
            >
              <MiniProgress percent={percentComplete} strokeWidth={8} />
            </ChartCard>
          </ColWrapper>
          <ColWrapper xs={24} sm={3} md={3}>
            <Card>
              <Statistic
                title="Users"
                value={users.length ? users.length : 0}
                prefix={<Icon type="team" />}
              />
            </Card>
          </ColWrapper>
        </Row>
        <Row>
          <ColWrapper xs={24} sm={14} md={14} lg={14} xl={14}>
            <Card
              style={{ height: 350 }}
              title={messages.dashboard.lastProcesses}
            >
              <Table
                size="small"
                bordered={false}
                pagination={false}
                columns={columns}
                dataSource={this.state.process}
              />
            </Card>
          </ColWrapper>
          <ColWrapper xs={24} sm={10} md={10} lg={10} xl={10}>
            <Card>
              <Pie
                hasLegend
                title={messages.dashboard.process.toUpperCase()}
                subTitle={messages.dashboard.process.toUpperCase()}
                total={() => (
                  <React.Fragment>
                    {numeral(
                      statusData.reduce((pre, now) => now.y + pre, 0)
                    ).format("0")}
                  </React.Fragment>
                )}
                data={statusData}
                height={265}
              />
            </Card>
          </ColWrapper>
        </Row>
      </Spin>
    );
  }
}

export default injectIntl(AdminDashboard);
